@font-face {
  src: url("DMSans-Bold.306cbdd3.ttf"), url("DMSans-Bold.306cbdd3.ttf");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  src: url("DMSans-Medium.b8d1a1bf.ttf"), url("DMSans-Medium.b8d1a1bf.ttf");
  font-weight: 500;
  font-display: swap;
  font-style: normal;
}

@font-face {
  src: url("DMSans-Regular.331f979e.ttf"), url("DMSans-Regular.331f979e.ttf");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  src: url("DMSans-Italic.ed07c4fe.ttf"), url("DMSans-Italic.ed07c4fe.ttf");
  font-weight: 400;
  font-display: swap;
  font-style: italic;
}

:root {
  --logo-theme-light: #00000;
  --logo-theme-dark: #f3f3f3;
  --dark-theme-background: #202024;
  --light-theme-background: #f6f6f6;
  --text-dark-color: #111;
  --text-light-color: #b4afaf;
  --accent-color1: #4f2ee8;
  --accent-color2: #eac645;
  --accent-color3: #fff;
  --main-font: "DM Sans";
  --header-background: #fff;
  --footer-background: #fff;
  --transition: 500ms cubic-bezier(.4, 0, .2, 1);
}

html {
  min-width: 320px;
}

body {
  letter-spacing: .02em;
  background-color: var(--light-theme-background);
  font-family: DM Sans, sans-serif;
  font-size: 12px;
}

.list {
  list-style: none;
}

.link {
  font-family: inherit;
  text-decoration: none;
}

p {
  margin-top: 0;
  margin-bottom: 0;
}

ul, ol {
  margin: 0;
  padding-left: 0;
}

h1, h2, h3, h4, h5, h6 {
  margin-top: 0;
  font-family: DM Sans, sans-serif;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.container {
  max-width: 100%;
  border: 1px solid red;
  margin-left: auto;
  margin-right: auto;
}

.is-hidden {
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  display: none;
}

@media screen and (min-width: 375px) {
  .container {
    width: 375px;
    padding-left: 20px;
    padding-right: 20px;
  }
}

@media screen and (min-width: 768px) {
  .container {
    width: 768px;
    padding-left: 32px;
    padding-right: 32px;
  }
}

@media screen and (min-width: 1440px) {
  .container {
    width: 1440px;
    padding-left: 24px;
    padding-right: 24px;
  }
}

.support-ukraine-container {
  width: 335px;
  height: 352px;
  background: radial-gradient(183.6% 183.01% at 59.76% -23.42%, #4f2ee8 18.03%, #fff 100%);
  border-radius: 16px;
  padding-top: 24px;
  padding-bottom: 20px;
  padding-left: 40px;
  position: absolute;
  top: 372px;
  left: 20px;
  box-shadow: 0 10px 10px rgba(245, 245, 247, .1);
}

.support-ukraine-text {
  flex-direction: row;
  align-items: flex-start;
  gap: 12px;
  margin-bottom: 40px;
  padding: 0;
  display: flex;
}

.support-ukraine-title {
  letter-spacing: -.04em;
  color: #7fffd4;
  font-size: 28px;
  line-height: 1.14;
}

.support-ukraine-gallery {
  counter-reset: my-awesome-counter;
  margin: 0;
  margin-bottom: px;
  flex-wrap: wrap;
  gap: 20px;
  padding: 0;
  list-style: none;
  display: flex;
}

.support_gallery_link_svg {
  height: 32px;
  width: 120px;
}

.support-ukraine-gallery li {
  height: 18px;
  color: #fff;
  counter-increment: my-awesome-counter;
  margin-bottom: .5rem;
  font-size: .8rem;
  line-height: 1.29;
  display: flex;
}

.support-ukraine-gallery li:before {
  content: "0" counter(my-awesome-counter);
  letter-spacing: -.04em;
  width: 14px;
  height: 18px;
  margin-right: 16px;
  font-size: 14px;
  line-height: 1.29;
}

.scroll-up-btn {
  width: 32px;
  height: 32px;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  transition-duration: 1s;
  display: flex;
  position: absolute;
  bottom: 20px;
  left: 152px;
}

.scroll-up-btn:hover {
  transform: rotate(180deg);
}

.scroll-up-icon {
  fill: #4f2ee8;
}

.book_card {
  border: 2px solid rgba(79, 46, 232, .4);
  border-radius: 16px;
  padding: 14px;
  display: flex;
}

.shopping_booklist {
  flex-direction: column;
  gap: 20px;
  display: flex;
}

.shopping_list_title {
  letter-spacing: -.04em;
  color: var(--text-dark-color);
  margin-bottom: 40px;
  font-size: 32px;
  font-weight: 700;
  line-height: 1.1875;
}

.shopping_list_title_span {
  color: var(--accent-color1);
}

.book-title {
  letter-spacing: -.04em;
  text-transform: uppercase;
  color: var(--text-dark-color);
  margin-bottom: 4px;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.125;
}

.book-category {
  letter-spacing: .03em;
  color: var(--text-light-color);
  margin-bottom: 8px;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.167;
}

.book-description {
  letter-spacing: -.04em;
  width: 193px;
  height: 68px;
  color: var(--text-dark-color);
  margin-bottom: 14px;
  font-size: 16px;
  font-weight: 400;
  line-height: 1.128;
}

.book-author {
  letter-spacing: -.04em;
  color: var(--text-light-color);
  font-size: 12px;
  font-style: italic;
  font-weight: 400;
  line-height: 1.17;
}

.book-image-div {
  width: 100%;
}

.market_places_div {
  height: 16px;
}

.market_placers_list {
  display: flex;
}

.marketplacer-icon-svg {
  height: 16px;
}

